import React, { useState, useEffect, useCallback } from 'react'
// import { Form, Input, Button, Checkbox, Space, message } from 'antd'
// import { UserOutlined, LockOutlined } from '@ant-design/icons';
import './signin-component.css'
import Footer from './footer'
import { signStore } from '../redux/redux'
import Sign from './sign'
import { Link } from "react-router-dom"

export default function Logup() {
  let [state, setState] = useState({
    form: 'signup',
    captcha: false,
    other: false,
    protocol: '',
    buttonText: '',
    sucessSignup: false,
  })
  useEffect(() => {
    let uns = signStore.subscribe(() => setState(signStore.getState()))
    return () => uns()
  })
  return (
    <div className="main">
      {state.sucessSignup &&
        <Success props={state} />
      }
      <img src="../images/login-top.png" alt="logo" className="logo" />
      <Sign state={state} />
      <Footer type="signup" />
    </div>
  )
}

export function Success(props) {
  let toSignin = useCallback((e) => {
    e.preventDefault()
    signStore.dispatch({
      type: 'toSignin',
      sucessSignup: false
    })
  }, [])
  return (
    <div className="success-bg">
      <div className={props.sucessSignup ? "success" : 'suc-transition success'}>
        <img src="/images/loading.gif" alt="" />
        <label className="label-visi">
          <span> </span>  <Link to="/sign" onClick={toSignin}></Link>
        </label>
      </div>
    </div>
  )
}
