import React, { useCallback, useEffect, useRef, useState } from 'react'
import axios from 'axios'
import { Link } from "react-router-dom"
import { Button } from 'antd'
import './user-page.css'
import { careStore, pageStore } from '../redux/redux'
import { useParams } from 'react-router-dom'

export default function ({ userId }) {
  let { id } = useParams()
  let [cares, setCare] = useState([])
  useEffect(() => {
    let uns = careStore.subscribe(() => setCare(careStore.getState().myCares))
    return () => uns()
  }, [])
  let outerRef = useRef()
  useEffect(() => {
    //
    axios.get(`/api/get-care/${userId}`)
      .then(res => {
        careStore.dispatch({
          type: 'getCare',
          myCares: res.data.myCares,
        })
      })
    //
    axios.get(`/api/invite/${id}`)
      .then(res => {
        pageStore.dispatch({
          type: 'getInitial',
          inviteList: res.data.inviteList,
        })
      })

  }, [id, userId])
  useEffect(() => {
    let foo = document.querySelector('.invite-outer')
    foo.onmousewheel = (e) => {
      e.preventDefault()
    }

    return () => foo.onmousewheel = null
  }, [])
  let inviteEnd = useCallback(() => {
    pageStore.dispatch({
      type: 'invite',
      invite: false,
    })
  }, [])
  useEffect(() => {
    function clearInvite(e) {
      e.stopPropagation()
      if (e.target === outerRef.current || e.target === document.body) {
        inviteEnd()
      }
    }
    window.addEventListener('click', clearInvite)
    return () => window.removeEventListener('click', clearInvite)
  }, [inviteEnd])
  return (
    <div className="invite-outer" ref={outerRef}>
      <div className="invite-main">
        <div className="invite-header"><h2><span> * </span></h2>
          <Button type="primary" size="middle" onClick={inviteEnd}></Button></div>
        <ul className="invite-list">
          {cares.length === 0 && <li className="no-invite"><span ></span></li>}
          {cares.map(invite => {
            return <li key={invite.createdAt}><InviteItem invite={invite} /></li>
          })}
        </ul>
      </div>
    </div>
  )
}

function InviteItem({ invite }) {
  let { id } = useParams()
  let [complete, setComplete] = useState(false)
  let [clear, setClear] = useState(false)
  useEffect(() => {
    function updateInvite() {
      let inviteList = pageStore.getState().inviteList || []
      let temp = inviteList.some(it => it.targetId === invite.careId)
      setComplete(temp)
    }
    updateInvite()
    let uns = pageStore.subscribe(() => updateInvite())
    return () => uns()
  }, [invite])

  let toInvite = useCallback((type) => {
    axios.post('/api/invite', {
      type: type,
      postId: id,
      targetId: invite.careId,
      targetName: invite.careName,
    }).then(res => {
      pageStore.dispatch({
        type: 'upDateInvite',
        inviteList: res.data.newInvites
      })
    })
  }, [id, invite])
  return (
    <div className="invite-item">
      <Link to={`/user/${invite.careId}`}><i style={{ backgroundImage: `url(${invite.avatar})` }} className="invite-avatar"></i></Link>
      <div className="invite-userInfo">
        <Link to={`/user/${invite.careId}`}><h4>{invite.careName}<span>  -  {invite.industry}</span></h4></Link>
        <span>{invite.sign}</span>
      </div>
      {complete
        ? <Button type="ghost" onClick={() => toInvite('revoke')} style={{ width: '100px', color: 'rgb(138 138 138)' }} onMouseEnter={() => setClear(true)} onMouseLeave={() => setClear(false)}>{clear ? '' : ''}</Button>
        : <Button type="ghost" style={{ width: '100px', borderColor: '#40a9ff', color: '#40a9ff' }} onClick={() => toInvite('active')}></Button>
      }

    </div>
  )
}
